.countdown_mobile_wrapper_landscape {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
}

.countdown_mobile_pill_landscape {
  padding-top: calc(5px + (10 - 5) * ((100vw - 568px) / (823 - 568)));
  width: calc(450px + (550 - 450) * ((100vw - 568px) / (823 - 568)));
}