.countdown_mobile_wrapper_portrait {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
}

.countdown_mobile_pill_portrait {
  padding-top: calc(100px + (200 - 100) * ((100vw - 320px) / (1920 - 320)));
  width: calc(300px + (400 - 300) * ((100vw - 320px) / (411 - 320)));
}
