.countdown_desktop_wrapper {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
}

.countdown_desktop_pill {
  padding-top: calc(50px + (100 - 50) * ((100vw - 320px) / (1920 - 320)));
  width: calc(450px + (900 - 450) * ((100vw - 500px) / (1920 - 500)));
}