.modal_wrapper {
  animation: modal_slide_in_from_top .3s ease-in-out 1;
  left: 50%;
  min-width: 50%;
  position: fixed;
  top: 10%;
  transform: translateX(-50%);
  width: calc(300px + (900 - 300) * ((100vw - 320px) / (1920 - 320)));
  z-index: 500;
}

.dismount_modal_wrapper {
  animation: modal_slide_out_to_top .3s ease-in-out 1;
  left: 50%;
  min-width: 50%;
  position: fixed;
  top: 10%;
  transform: translateX(-50%);
  width: calc(300px + (900 - 300) * ((100vw - 320px) / (1920 - 320)));
  z-index: 500;
}