input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.input:-internal-autofill-selected {
  width: 25%;
  text-rendering: auto;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  text-align: start;
  box-sizing: unset;
  align-items: center;
  white-space: normal;
  background-color: #fff;
  cursor: default;
  margin: 0em;
  color: #000;
  line-height: 1.3;
  padding: 5px;
  border: 1px solid rgba(175, 175, 175, 1);
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}

a:-webkit-any-link {
  cursor: pointer;
  text-decoration: none;
}

.blank {
  opacity: 0;
  visibility: hidden;
}

