.dismount_countdown_desktop_timer {
  animation: dismount_countdown_desktop_timer .5s ease-in-out 1;
  background-color: rgba(255,255,255, 1);
  background-image: linear-gradient( rgb(50, 50, 50, 1) 10%,rgb(0, 0, 0, 1) 100%);
}

@keyframes dismount_countdown_desktop_timer {
  0% {
    transform: translateX(0px);
  }
  25% {
    transform: translateX(2000px);
  }
  100% {
    transform: translateX(2000px);
  }
}